rtaleza emocional y el desapego de las pasiones. Entre los filósofos estoicos más influyentes se encuentran: Séneca, Epicteto y el emperador Marco Aurelio, quienes dejaron enseñanzas que aún hoy inspiran a personas de todo el mundo.")])]),_c('p',[_vm._v("Los principios fundamentales del estoicismo, son:")])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/6.png"),"data-aos":"zoom-in"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"bg-color-2 px-4 py-5 mb-4",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("El estoicismo no es solo una teoría filosófica, sino una práctica de vida. Una de las aplicaciones más comunes del estoicismo en la vida cotidiana, es el manejo de las emociones. El estoico no busca reprimirlas, sino comprenderlas y gobernarlas mediante la razón. Cuando una persona enfrenta una frustración, como una discusión laboral o una contrariedad inesperada, puede aplicar el principio estoico de distinguir entre lo que puede cambiar (su actitud, su respuesta emocional) y lo que no (la conducta del otro, las circunstancias externas). Esta forma de pensar ayuda a evitar el sufrimiento innecesario y a tomar decisiones más equilibradas y racionales, sin dejarse llevar por el enojo, el miedo o la ansiedad.")])]),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Otro aspecto clave del estoicismo en la cotidianidad, es el ejercicio del desapego y la gratitud. Los estoicos recuerdan constantemente que todo es efímero: la salud, las posesiones, incluso las relaciones. Este pensamiento no conduce al pesimismo, sino a una mayor apreciación de lo que se tiene mientras se tiene, y a una mayor fortaleza cuando se pierde. Practicar la gratitud estoica implica valorar lo esencial, vivir con sobriedad y no dejar que el deseo de cosas externas perturbe la tranquilidad 